import React, { Component } from 'react';
import { inject, observer } from 'mobx-react';
import { Divider, Modal, message, Badge, Table } from 'antd';
import { toJS } from 'mobx';


@inject('SystemStore')
@observer
class DepartmentTable extends Component {
    render() {
        const { tableData, isLoading } = toJS(this.props.SystemStore)
        return <div>
            <Table rowKey="id" size='middle' autoExpandParent={false} loading={isLoading} columns={this.columns()} dataSource={tableData} />
        </div>;
    }
    delete = (record) => {
        const { deleteCol, getDeptData, columnsType, deleteCus } = this.props.SystemStore
        Modal.confirm({
            title: '提示',
            content: '是否确认删除该条数据？',
            okText: '确认',
            cancelText: '取消',
            onOk: () => {
                if (columnsType === '1') {
                    deleteCol({ id: record.id }).then(res => {
                        message.success('删除成功')
                        getDeptData(columnsType)
                    })
                } else {
                    deleteCus({ id: record.id }).then(res => {
                        message.success('删除成功')
                        getDeptData(columnsType)
                    })
                }

            },
        });

    }
    addLower = (record) => {
        let obj = { parentId: record.id, delFlag: 1 }
        this.props.SystemStore.modalChange(1, obj)
    }
    componentDidMount() {
        const { getDeptData } = this.props.SystemStore
        getDeptData('1')
    }
    columns = () => {
        console.log(this.props.SystemStore.columnsType)
        if (this.props.SystemStore.columnsType === '1') {
            return [
                {
                    title: '部门名称',
                    dataIndex: 'name',
                    key: 'name',
                },
                {
                    title: '状态',
                    dataIndex: 'delFlag',
                    key: 'delFlag',
                    render: (text, record) => (
                        <div>
                            {text ? <Badge status="success" text="正常" />
                                : <Badge status="error" text="禁用" />}
                        </div>
                    )
                },
                {
                    title: '操作',
                    key: 'x',
                    align: 'right',
                    render: (text, record, index) => (
                        <span>
                            <a onClick={e => this.props.SystemStore.modalChange(1, record)}>编辑</a>
                            <Divider type="vertical" />
                            <a onClick={e => this.addLower(record)}>增加下级</a>
                            <Divider type="vertical" />
                            <a onClick={e => this.changeStatus(index, record)}>{record.delFlag ? '禁用' : '启用'}</a>
                            <Divider type="vertical" />
                            <a onClick={e => this.delete(record)}>删除</a>
                        </span>
                    )

                }
            ]
        } else {
            return [
                // {
                //     title: '合作伙伴编码',
                //     dataIndex: 'customerCode',
                //     key: 'customerCode',
                // },
                {
                    title: '合作伙伴名称',
                    dataIndex: 'customerName',
                    key: 'customerName',
                },
                {
                    title: '地址',
                    dataIndex: 'customerAddress',
                    key: 'customerAddress',
                    render: (text) => (
                        <div>{text ? text : '-'}</div>
                    )
                },
                {
                    title: '联系人',
                    dataIndex: 'customerContacts',
                    key: 'customerContacts',
                    render: (text) => (
                        <div>{text ? text : '-'}</div>
                    )
                },
                // {
                //     title: '联系人职务',
                //     dataIndex: 'contactPost',
                //     key: 'contactPost',
                //     render: (text) => (
                //         <div>{text ? text : '-'}</div>
                //     )
                // },
                {
                    title: '联系方式',
                    dataIndex: 'customerPhone',
                    key: 'customerPhone',
                    render: (text) => (
                        <div>{text ? text : '-'}</div>
                    )
                },
                // {
                //     title: '邮箱',
                //     dataIndex: 'customerEmails',
                //     key: 'customerEmails',
                //     render: (text) => (
                //         <div>{text ? text : '-'}</div>
                //     )
                // },
                // {
                //     title: '备注',
                //     dataIndex: 'description',
                //     key: 'description',
                // },
                {
                    title: '状态',
                    dataIndex: 'customerStatus',
                    key: 'customerStatus',
                    render: (text, record) => (
                        <div>
                            {text ? <Badge status="success" text="启用" />
                                : <Badge status="error" text="禁用" />}
                        </div>
                    )
                },
                {
                    title: '操作',
                    key: 'x',
                    align: 'right',
                    render: (text, record) => (
                        <span>
                            <a onClick={e => this.reviewDetail(record)}>详情</a>
                            <Divider type="vertical" />
                            <a onClick={e => this.props.SystemStore.modalChange(1, record)}>编辑</a>
                            <Divider type="vertical" />
                            <a onClick={e => this.delete(record)}>删除</a>
                        </span>
                    )
                }
            ]
        }
    }
    reviewDetail = (record) => {
        this.props.SystemStore.isReview = true
        this.props.SystemStore.modalChange(1, record)
    }
    changeStatus = (index, record) => {
        this.props.SystemStore.addNew(0, { ...record, delFlag: record.delFlag ? 0 : 1 }).then(res => {
            if (res) {
                message.success(record.delFlag ? '禁用成功' : '启用成功')
                const { getDeptData, columnsType, customerName } = this.props.SystemStore
                getDeptData(columnsType, { customerName })
            }
        })
    }
}
export default DepartmentTable;