import React, { Component } from 'react';
import { inject, observer } from 'mobx-react';
import { toJS } from 'mobx';
import { message, Form, Input, Radio, Select } from 'antd';
import GlobalModal from 'Components/GlobalModal';
@inject('SystemStore')
@observer
class Department extends Component {
    render() {
        const { modalVisible, formData, columnsType, arr, isReview } = this.props.SystemStore
        const formLabelCol = { span: 4 }
        const formWrapperCol = { span: 18 }
        const { getFieldDecorator } = this.props.form;
        const { Option } = Select
        console.log(arr)
        return <div>
            {
                modalVisible && <GlobalModal
                    title={JSON.stringify(formData) === '{}' ? '新增' : '编辑'}
                    visible={modalVisible}
                    width={'50%'}
                    onOk={this.handleSubmit}
                    onCancel={e => this.props.SystemStore.modalChange(0)}
                    children={
                        <div>
                            <Form onSubmit={this.handleSubmit} layout='horizontal' labelCol={formLabelCol} wrapperCol={formWrapperCol}>
                                {columnsType === '1' ? <div>
                                    <Form.Item label="上级部门">
                                        {getFieldDecorator('parentId', {
                                            initialValue: formData.parentId || '总部门'
                                        })(
                                            <Select disabled>
                                                {arr.map(item => (<Option value={item.id} key={item.id}>{item.name}</Option>))}
                                            </Select>
                                        )}
                                    </Form.Item>
                                    <Form.Item label="部门名称">
                                        {getFieldDecorator('name', {
                                            rules: [{ required: true, message: '请输入部门名称' }],
                                            initialValue: formData.name || ''
                                        })(
                                            <Input placeholder="请输入部门名称" />
                                        )}
                                    </Form.Item>
                                    <Form.Item label="状态">
                                        {getFieldDecorator('delFlag', {
                                            rules: [{ required: false, message: '请选择状态' }],
                                            initialValue: formData.delFlag || 0
                                        })(
                                            <Radio.Group>
                                                <Radio value={1}>启用</Radio>
                                                <Radio value={0}>禁用</Radio>
                                            </Radio.Group>
                                        )}
                                    </Form.Item>
                                </div> : <div>
                                        {<Form.Item label="合作伙伴编码">
                                            {getFieldDecorator('customerCode', {
                                                rules: [{ required: true, message: '请输入合作伙伴编码' }],
                                                initialValue: formData.customerCode || ''
                                            })(
                                                <Input placeholder="请输入合作伙伴编码" />
                                            )}
                                        </Form.Item>}
                                        <Form.Item label="合作伙伴名称">
                                            {getFieldDecorator('customerName', {
                                                rules: [{ required: true, message: '请输入合作伙伴名称' }],
                                                initialValue: formData.customerName || ''
                                            })(
                                                <Input readOnly={isReview} placeholder="请输入合作伙伴名称" />
                                            )}
                                        </Form.Item>
                                        <Form.Item label="联系人">
                                            {getFieldDecorator('customerContacts', {
                                                rules: [{ required: true, message: '请输入联系人' }],
                                                initialValue: formData.customerContacts || ''
                                            })(
                                                <Input readOnly={isReview} placeholder="请输入联系人" />
                                            )}
                                        </Form.Item>
                                        <Form.Item label="联系人职务">
                                            {getFieldDecorator('contactPost', {
                                                rules: [{ required: false, message: '请输入联系人职务' }],
                                                initialValue: formData.contactPost || ''
                                            })(
                                                <Input readOnly={isReview} placeholder="请输入联系人职务" />
                                            )}
                                        </Form.Item>
                                        <Form.Item label="联系方式">
                                            {getFieldDecorator('customerPhone', {
                                                rules: [{ required: false, message: '请输入联系方式' }],
                                                initialValue: formData.customerPhone || ''
                                            })(
                                                <Input readOnly={isReview} placeholder="请输入联系方式" />
                                            )}
                                        </Form.Item>
                                        <Form.Item label="联系邮件">
                                            {getFieldDecorator('customerEmails', {
                                                rules: [{ required: false, message: '请输入联系邮件' }],
                                                initialValue: formData.customerEmails || ''
                                            })(
                                                <Input readOnly={isReview} placeholder="请输入联系邮件" />
                                            )}
                                        </Form.Item>
                                        <Form.Item label="地址">
                                            {getFieldDecorator('customerAddress', {
                                                rules: [{ required: false, message: '请输入地址' }],
                                                initialValue: formData.customerAddress || ''
                                            })(
                                                <Input readOnly={isReview} placeholder="请输入地址" />
                                            )}
                                        </Form.Item>
                                        <Form.Item label="备注">
                                            {getFieldDecorator('description', {
                                                rules: [{ required: false, message: '请输入备注' }],
                                                initialValue: formData.description || ''
                                            })(
                                                <Input readOnly={isReview} placeholder="备注" />
                                            )}
                                        </Form.Item>
                                        <Form.Item label="状态">
                                            {getFieldDecorator('customerStatus', {
                                                rules: [{ required: false, message: '请选择状态' }],
                                                initialValue: formData.customerStatus || 0
                                            })(
                                                <Radio.Group disabled={isReview}>
                                                    <Radio value={1}>启用</Radio>
                                                    <Radio value={0}>禁用</Radio>
                                                </Radio.Group>
                                            )}
                                        </Form.Item>
                                    </div>
                                }
                            </Form>
                        </div>
                    }
                />
            }
        </div>;
    }
    //提交表单
    handleSubmit = e => {
        e.preventDefault();
        const { getDeptData, modalChange, addNew, formData, isReview, columnsType, addNewCustomer } = this.props.SystemStore
        this.props.form.validateFields((err, values) => {
            if (!err) {
                if (columnsType === '1') {
                    if (formData.id) {
                        values.parentId = values.parentId === '总部门' ? 0 : values.parentId
                        addNew(0, { ...formData, ...values }).then(res => {
                            getDeptData(columnsType)
                            message.success('更新成功')
                            modalChange(0)
                        })
                    } else {
                        values.parentId = values.parentId === '总部门' ? 0 : values.parentId
                        addNew(1, values).then(res => {
                            getDeptData(columnsType)
                            message.success('添加成功')
                            modalChange(0)
                        })
                    }
                } else {
                    if (isReview) {
                        this.props.SystemStore.isReview = false
                        modalChange(0)
                        return
                    }
                    if (formData.id) {
                        addNewCustomer(0, { ...formData, ...values }).then(res => {
                            getDeptData(columnsType)
                            message.success('更新成功')
                            modalChange(0)
                        })
                    } else {
                        addNewCustomer(1, { ...values }).then(res => {
                            getDeptData(columnsType)
                            message.success('添加成功')
                            modalChange(0)
                        })
                    }
                }
            }
        });
    };
}
const DepartmentForm = Form.create({ name: 'define' })(Department);
export default DepartmentForm;